@import url("https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply font-space;
  }
}
@layer components {
  .NavbarIcon {
    @apply flex relative items-center justify-center h-12 w-12 rounded-3xl my-5 mx-auto bg-gray-700 text-purple-400
    hover:bg-purple-400 hover:text-gray-700 hover:rounded-none transition-all ease-linear duration-200;
  }
  .NavbarTip {
    @apply fixed h-auto w-auto min-w-max p-2 m-2 left-14 shadow-lg bg-gray-700 text-zinc-200 text-xs font-bold
    scale-0 group-hover:scale-100 transition-all ease-linear duration-100;
  }
  .blueIcon {
    @apply hover:bg-blue-400 text-blue-400;
  }

  .greenIcon {
    @apply hover:bg-green-400 text-green-400;
  }
}
